<template>
  <div class="weather-box">
    <img src="../assets/cloud.png">
    <p class="temperature"></p>
    <p class="description"></p>
  </div>
</template>

<script>
export default {
  name: 'WeatherBox',
  props: {
    controller: Boolean
  },
  data () {
    return {
      weatherBox: null,
      image: null,
      temperature: null,
      description: null,
      data: []
    }
  },
  mounted () {
    this.weatherBox = document.querySelector('.weather-box')
    this.image = document.querySelector('.weather-box img')
    this.temperature = document.querySelector('.weather-box .temperature')
    this.description = document.querySelector('.weather-box .description')
  },
  methods: {
    sendWeatherBox () {
      this.data = [this.weatherBox, this.image, this.temperature, this.description]
      this.$emit('sendWeatherBox', this.data)
    }
  }
}
</script>

