<template>
  <div class="container">
    <SearchBox :container="container" @btnClicked="clickEvent" :weather-box="wBox" :weather-details="wDetails" :error404="nFound" :image="wImg"
    :temperature="wTemp" :descriptionBox="wDesc" :humidity="wHum" :wind="wWind"></SearchBox>
    <NotFound ref="notFound" @sendNotFound="NotFoundEvent"></NotFound>
    <WeatherBox ref="weatherBox" @sendWeatherBox="WeatherBoxEvent"></WeatherBox>
    <Details ref="details" @sendDetails="DetailsEvent"></Details>
  </div>
</template>

<script>
import SearchBox from '@/components/SearchBox.vue'
import NotFound from '@/components/NotFound.vue'
import Details from '@/components/Details.vue'
import WeatherBox from '@/components/WeatherBox.vue'
export default {
  name: 'Weather',
  components: { WeatherBox, Details, NotFound, SearchBox },
  data () {
    return {
      container: null,
      wBox: null,
      wDetails: null,
      nFound: null,
      wImg: null,
      wTemp: null,
      wDesc: null,
      wHum: null,
      wWind: null
    }
  },
  mounted () {
    this.container = document.querySelector('.container')
  },
  methods: {
    clickEvent () {
      this.$refs.weatherBox.sendWeatherBox()
      this.$refs.details.sendDetails()
      this.$refs.notFound.sendNotFound()
    },
    WeatherBoxEvent (data) {
      this.wBox = data[0]
      this.wImg = data[1]
      this.wTemp = data[2]
      this.wDesc = data[3]
    },
    DetailsEvent (data) {
      this.wDetails = data[0]
      this.wHum = data[1]
      this.wWind = data[2]
    },
    NotFoundEvent (data) {
      this.nFound = data
    }
  }
}
</script>

<style>
  *{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    border: 0;
    outline: none;
  }
  body {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #06283D;
  }
  .container{
    position:relative;
    width: 400px;
    height: 105px;
    background: #fff;
    padding: 28px 32px;
    overflow: hidden;
    border-radius: 18px;
    font-family: 'Open Sans', sans-serif;
    transition: 0.6s ease-out;
  }
</style>
