<template>
  <div class="search-box">
    <font-awesome-icon icon="fa-solid fa-location-dot" class="svg1"></font-awesome-icon>
    <input type="text" placeholder="Enter your location">
    <button @click="searchWeather"><font-awesome-icon icon="fa-solid fa-magnifying-glass"></font-awesome-icon></button>
  </div>
</template>

<script>
export default {
  name: 'SearchBox',
  props: {
    container: HTMLDivElement,
    weatherBox: HTMLDivElement,
    weatherDetails: HTMLDivElement,
    error404: HTMLDivElement,
    image: HTMLImageElement,
    temperature: HTMLParagraphElement,
    descriptionBox: HTMLParagraphElement,
    humidity: HTMLSpanElement,
    wind: HTMLSpanElement
  },
  data () {
    return {
      search: null
    }
  },
  mounted () {
    this.search = document.querySelector('.search-box button')
  },
  methods: {
    searchWeather () {
      this.$emit('btnClicked')
      const APIKey = '24286d6b487a38cf0fa9dd0a7ab61577'
      const city = document.querySelector('.search-box input').value

      if (city === '') {
        return
      }

      fetch(`https://api.openweathermap.org/data/2.5/weather?q=${city}&appid=${APIKey}`).then(response => response.json()).then(json => {
        console.log(json)
        if (json.cod === '404') {
          this.container.style.height = '400px'
          this.weatherBox.style.display = 'none'
          this.weatherDetails.style.display = 'none'
          this.error404.style.display = 'block'
          this.error404.classList.add('fadeIn')
        } else {
          this.error404.style.display = 'none'
          this.error404.classList.remove('fadeIn')

          switch (json.weather[0].main) {
            case 'Clear':
              this.image.src = 'https://raw.githubusercontent.com/AsmrProg-YT/100-days-of-javascript/master/Day%20%2310%20-%20Weather%20App/images/clear.png'
              break
            case 'Rain':
              this.image.src = 'https://github.com/AsmrProg-YT/100-days-of-javascript/blob/master/Day%20%2310%20-%20Weather%20App/images/rain.png?raw=true'
              break
            case 'Snow':
              this.image.src = 'https://github.com/AsmrProg-YT/100-days-of-javascript/blob/master/Day%20%2310%20-%20Weather%20App/images/snow.png?raw=true'
              break
            case 'Clouds':
              this.image.src = 'https://github.com/AsmrProg-YT/100-days-of-javascript/blob/master/Day%20%2310%20-%20Weather%20App/images/cloud.png?raw=true'
              break
            case 'Haze':
              this.image.src = 'https://github.com/AsmrProg-YT/100-days-of-javascript/blob/master/Day%20%2310%20-%20Weather%20App/images/mist.png?raw=true'
              break
            default:
              this.image.src = ''
              break
          }
          let temperature = parseInt(json.main.temp)
          temperature = temperature - 273.15
          const roundedTemp = temperature.toFixed(2)
          this.temperature.innerHTML = `${roundedTemp}<span>°C</span>`
          this.descriptionBox.innerHTML = `${json.weather[0].description}`
          this.humidity.innerHTML = `${json.main.humidity}%`
          this.wind.innerHTML = `${parseInt(json.wind.speed)}Km/h`
          this.weatherBox.style.display = 'block'
          this.weatherDetails.style.display = 'flex'
          this.weatherBox.classList.add('fadeIn')
          this.weatherDetails.classList.add('fadeIn')
          this.container.style.height = '590px'
        }
      })
    }
  }
}
</script>

<style scoped>
  .search-box{
    width: 100%;
    height: min-content;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .search-box input {
    color: #06283D;
    width: 80%;
    font-size: 24px;
    font-weight: 500;
    text-transform: uppercase;
    padding-left: 32px;
  }
  .search-box input::placeholder {
    font-size: 20px;
    font-weight: 500;
    color: #06283D;
    text-transform: capitalize;
  }
  .search-box button{
    cursor: pointer;
    width: 50px;
    height: 50px;
    color: #06283D;
    background: #dff6ff;
    border-radius: 50%;
    font-size: 22px;
    transition: 0.4s ease;
  }
  .search-box button:hover{
    color: #fff;
    background: #06283D;
  }
  .search-box .svg1{
    position: absolute;
    color: #06283D;
    font-size: 28px;
  }
</style>
