<template>
  <div class="weather-details">
    <div v-for="(element,index) in details" :key="index" :class="element">
      <font-awesome-icon v-if="element === 'humidity'" icon="fa-solid fa-water"></font-awesome-icon>
      <font-awesome-icon v-else icon="fa-solid fa-wind"></font-awesome-icon>
      <div class="text">
        <span></span>
        <p v-if="element === 'humidity'">Humidity</p>
        <p v-else>Wind Speed</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Details',
  data () {
    return {
      details: ['humidity', 'wind'],
      wdetails: null,
      whumidity: null,
      wwind: null,
      data: []
    }
  },
  mounted () {
    this.wdetails = document.querySelector('.weather-details')
    this.whumidity = document.querySelector('.weather-details .humidity span')
    this.wwind = document.querySelector('.weather-details .wind span')
  },
  methods: {
    sendDetails () {
      this.data = [this.wdetails, this.whumidity, this.wwind]
      this.$emit('sendDetails', this.data)
    }
  }
}

</script>

